'use strict';

export interface ToolbarNewInfo {
  /**
   * Show/Hide toolbar, only take effect if its value !== undefined.
   */
  showToolbar?: boolean;
  /**
   * User is VIP or not, only take effect if its value !== undefined.
   */
  isVip?: boolean;
  /**
   * The count of new messages, only take effect if its value !== undefined.
   */
  newMsgCount?: number;
  /**
   * The title of toolbar button.
   */
  buttonTitle?: string;
}
